<style scoped>
.card-box {
    text-align: left;
    margin: 5px auto;
}
</style>
<template>
<div class="page-box">
    <sub-title title="个人中心" />
    <div class="card-box">
        <Card>
            <p slot="title">个人信息</p>
            <!-- <div slot="extra">
         <Button size="small" type="warning" @click="modifyInfo">修改信息</Button>
         <Button size="small" style="margin-left:8px;" type="error" @click="correctPassword">修改密码</Button>
       </div> -->
            <div>
                <Row>
                    <Col span="4">
                    账户名：
                    </Col>
                    <Col span="8">
                    {{userName}}
                    </Col>
                    <Col span="4">
                    姓名：
                    </Col>
                    <Col span="8">
                    {{realName}}
                    </Col>
                </Row>
                <Row>
                    <Col span="4">
                    账户类型：
                    </Col>
                    <Col span="8">
                    {{userType}}
                    </Col>
                    <Col span="4">
                    院系归属：
                    </Col>
                    <Col span="8">
                    {{deptString}}
                    </Col>
                </Row>
            </div>
        </Card>
    </div>
</div>
</template>

<script>
import SubTitle from "../components/SubTitle";
export default {
    data() {
        return {
            userName: "",
            realName: "",
            userType: "",
            deptString: '',
            userInfo: {},
            deptMap: null,
        };
    },
    beforeMount() {
        this.getDeptInfo().then((res) => {
            if (res) this.getRecentUser();
        })
    },
    components: {
        SubTitle
    },
    methods: {
        getDeptInfo() {
            return new Promise((resolve, reject) => {
                this.deptMap = Object.create(null);
                this.$http.get("/admin/dept/getAllDept").then(res => {
                    console.log("dept info", res.data);
                    if (res.data.status == 200) {
                        let deptArr = res.data.data;
                        deptArr.forEach(item => {
                            this.deptMap[item.deptId] = item.deptName;
                        });
                        resolve(true);
                    } else {
                        this.$Message.error({
                            content: "网络错误"
                        });
                        reject(false);
                    }
                });
            });
        },
        getRecentUser() {
            this.$http.get("/admin/getCurrentUser").then(res => {
                console.log("====================================");
                console.log("/admin/getCurrentUser", res.data);
                if (res.data.status == 200) {
                    this.userInfo = res.data.data;
                    this.userName = this.userInfo.userName;
                    this.realName = this.userInfo.realName;
                    switch (this.userInfo.userType) {
                        case "1":
                            this.userType = "管理员";
                            break;
                        case "3":
                            this.userType = "老师";
                            break;
                        case "5":
                            this.userType = "学生";
                            break;
                        default:
                            this.userType = "未知";
                            break;
                    }
                    if (this.userInfo.academyId == -1) {
                        this.deptString = '无部门'
                    } else {
                        this.deptString = `${this.deptMap[res.data.data.academyId]}/${this.deptMap[res.data.data.gradeId]}/${this.deptMap[res.data.data.classId]}`
                    }
                }
                console.log("====================================");
            });
        },
        modifyInfo() {},
        correctPassword() {}
    }
};
</script>
