<style scoped>
.set-box {
  min-height: 340px;
  user-select: none;
}

.form {
  width: 80%;
  margin: 0 auto;
  margin-top: 20px;
  text-align: left;
}

.add-item {
  margin-top: 20px;
}

.add-btn {
  margin-left: 20px;
}
</style>
<template>
<div>
   <Row>
     <Col span="24">
     <sub-title title="学校设置" />
      </Col>
   </Row>
   <Alert type="warning">请在左侧点选相应的节点，选择修改或新增(新增院系就选择学校，新增年级就选择相应的院系,请确保每个结构都有班级)</Alert>
    <Row>
      <Col class="set-box" span="10">
        <h4>当前设置</h4>
        <div class="form">
         <Tree :data="treeData" ref="tree" @on-select-change="selectChange"></Tree>
        </div>
        

      </Col>
      <Col class="set-box" span="14">
        <h4>更新设置</h4>
        <div class="form">
          <Row type='flex' align="middle" justify="center">
            <Col span="24">
              
            </Col>
            <Col v-if="selectNodeType==='school'" class="add-item" span="24">
            <Input v-model="newAcademy" placeholder="新增院系" clearable style="width: 100px" />
            <Button class="add-btn" type="info" @click="addNewAcademy">新增院系</Button>
            
            </Col>
            <Col v-if="selectNodeType==='academy'" class="add-item" span="24">
              <Input v-model="newGrade" placeholder="新增年级" clearable style="width: 100px" />
              <Button class="add-btn" type="info" @click="addNewGrade">新增年级</Button>
            
            </Col>
            <Col  v-if="selectNodeType==='grade'" class="add-item" span="24">
              <Input v-model="newClass" placeholder="新增班级" clearable style="width: 100px" />
              <Button class="add-btn" type="info" @click="addNewClass">新增班级</Button>
            
            </Col>
          <Col v-if="selectNodeType!=='school'&&selectNodeType!=='none'" class="add-item" span="24">
            <Button type="warning" @click="modifySelected">修改选中项</Button>
            <Button type="error" @click="deleteSelected">删除选中项</Button>
          </Col>
          </Row>
          
        </div>
      </Col>
    </Row>
    <Row>
      <Col :push="18" span="6">
        <Button type="warning" style="margin-left:15px;" @click="refreshDept">刷新结构</Button>
      </Col>
    </Row>
    </div>
</template>

<script>
import SubTitle from "../components/SubTitle";
export default {
  data() {
    return {
      selectNodeType: "none",
      selectNode: null,
      newAcademy: "",
      newGrade: "",
      newClass: "",
      treeData: []
    };
  },
  beforeMount() {
    this.getDeptTree();
  },
  components: {
    SubTitle
  },
  methods: {
    refreshDept() {
      this.selectNodeType = "none";
      this.selectNode = null;
      this.getDeptTree();
    },
    selectChange() {
      let selected = this.$refs["tree"].getSelectedNodes();
      console.log(selected);
      if (selected.length > 0) {
        this.selectNode = selected[0];
        this.selectNodeType = selected[0].nodeType;
      } else {
        this.selectNodeType = "none";
      this.selectNode = null;
      }
    },
    async addNewAcademy() {
      let title = this.newAcademy;
      if (title.length < 1) return;

      if (await this.addNewDept(title, 1, -1)) {
        this.refreshDept();
        this.newAcademy = "";
      }
    },
    async addNewGrade() {
      let title = this.newGrade;
      if (title.length < 1) return;
      let selected = this.$refs["tree"].getSelectedNodes();
      if (selected.length > 0 && selected.length < 2) {
        let theNode = selected[0];
        let parentId = theNode.deptId;
        if (await this.addNewDept(title, 3, parentId)) {
          this.refreshDept();
          this.newGrade = "";
        }
      }
    },
    async addNewClass() {
      let title = this.newClass;
      if (title.length < 1) return;
      let selected = this.$refs["tree"].getSelectedNodes();
      if (selected.length > 0 && selected.length < 2) {
        let theNode = selected[0];
        let parentId = theNode.deptId;
        if (await this.addNewDept(title, 5, parentId)) {
          this.refreshDept();
          this.newClass = "";
        }
      }
    },
    getSelectItems() {
      let sth = this.$refs.tree.getCheckedNodes();
      console.log("select", sth);
      return sth;
    },
    deleteSelected() {
      let selected = this.$refs["tree"].getSelectedNodes();
      console.log(selected);
      if (selected.length > 0 && selected.length < 2) {
        let theNode = selected[0];
        if (theNode.deptType === 5) {
          if (!theNode.studentNum || theNode.studentNum === 0) {
            this.$Modal.confirm({
              title: "删除确认",
              content: `是否确认删除${theNode.deptName}`,
              onOk: () => {
                this.deleteDept(theNode.deptId);
              }
            });
          } else {
            this.$Message.error({
              content: "该班级下还有学生"
            });
          }
        } else {
          if (!theNode.children || theNode.children.length === 0) {
            console.log("可以删除");
            this.$Modal.confirm({
              title: "删除确认",
              content: `是否确认删除${theNode.deptName}`,
              onOk: () => {
                this.deleteDept(theNode.deptId);
              }
            });
          } else {
            this.$Message.error({
              content: "该部门下还有分支"
            });
          }
        }
      } else {
        this.$Message.error("请选中一项操作");
      }
    },
    modifySelected() {
      console.log("修改选中");
      this.modifyValue = this.selectNode.deptName;
      this.$Modal.confirm({
        render: h => {
          return h("Input", {
            props: {
              value: this.modifyValue,
              autofocus: true,
              placeholder: "输入想要修改的名称"
            },
            on: {
              input: val => {
                this.modifyValue = val;
              }
            }
          });
        },
        onCancel: () => {
          console.log("点了取消");
          this.modifyValue = "";
        },
        onOk: async () => {
          console.log("点了确定");

          if (this.modifyValue.length > 0) {
            let { deptId, deptType, parentId } = this.selectNode;
            let newTitle = this.modifyValue;
            if (await this.modifyDept(deptId, newTitle, deptType, parentId)) {
              this.selectNode.title = newTitle;
              this.selectNode.deptName = newTitle;
              this.modifyValue = "";
              this.$Message.success("修改成功");
            } else {
              this.$Message.info("修改失败");
            }
          } else {
            this.$Message.info("请检查输入");
          }
        }
      });
    },

    addNewDept(deptName, deptType, parentId) {
      return new Promise((resolve, reject) => {
        this.$http
          .post("/admin/dept/saveDept", {
            parentId,
            deptName,
            deptType,
            deptDetail: ""
          })
          .then(res => {
            console.log(res.data);
            if (res.data.status == 200) {
              this.$Message.success("新增成功");
              resolve(true);
            } else {
              reject(false);
            }
          });
      });
    },
    modifyDept(deptId, deptName, deptType, parentId) {
      return new Promise((resolve, reject) => {
        this.$http
          .post("/admin/dept/updateDept", {
            deptId,
            parentId,
            deptName,
            deptType,
            deptDetail: ""
          })
          .then(res => {
            console.log(res.data);
            if (res.data.status == 200) {
              resolve(true);
            } else {
              reject(false);
            }
          });
      });
    },
    deleteDept(deptId) {
      return new Promise((resolve, reject) => {
        this.$http.post(`/admin/dept/deleteDept?deptId=${deptId}`).then(res => {
          console.log(res.data);
          if (res.data.status == 200) {
            this.refreshDept();
            resolve(true);
          } else {
            reject(false);
          }
        });
      });
    },
    formatTreeData(tracedTreeArr) {
      this.treeData = [];
      let root = {
        title: "学校",
        expand: true,
        nodeType: "school",
        children: tracedTreeArr
      };
      this.treeData.push(root);
    },
    dealTreeData(selectArr) {
      return selectArr.filter(value => {
        return !value.disableCheckbox;
      });
    },
    getDeptTree() {
      this.$http.get("/admin/dept/getDeptNodeTree").then(res => {
        console.log("dept info", res.data);
        if (res.data.status == 200) {
          let deptTree = res.data.data;
          console.log("dept tree", deptTree);
          let movedDeptTree = moveUnToLast(deptTree);

          let tracedTreeArr = traceDeptTree(movedDeptTree);
          this.formatTreeData(tracedTreeArr);
        } else {
          this.$Message.error({
            content: "网络错误"
          });
        }
      });
    }
  }
};

function moveUnToLast(treeArr) {
  let newTreeArr = treeArr.slice(0);
  let unIndex = newTreeArr.findIndex(item => {
    return item.deptType === 0;
  });
  let unItem = newTreeArr[unIndex];
  newTreeArr.splice(unIndex, 1);
  newTreeArr.push(unItem);
  return newTreeArr;
}

function traceDeptTree(treeArr) {
  let newTreeArr = treeArr.slice(0);
  newTreeArr.forEach(item => {
    item.title = item.deptName;
    item.deptId = item.deptId;

    switch (item.deptType) {
      case 1:
        item.nodeType = "academy";
        //item.expand = true;
        break;
      case 3:
        item.nodeType = "grade";
        break;
      case 5:
        item.nodeType = "class";
        item.title = `${item.deptName} =>人员${
          item.studentNum ? item.studentNum : 0
        }名`;
        break;
      default:
        item.nodeType = "none";
        break;
    }
    if (item.children) {
      traceDeptTree(item.children);
    } else {
      delete item.children;
    }
  });

  return newTreeArr;
}
</script>

