<style scoped>
.total-canvas {
}
</style>
<template>
 <div>
   <sub-title title="信息总览" />
   <div class="overview-box">
     <Row>
         <Col span="12">
         <h3>全校数据</h3>
             <div class="total-canvas" id="c1"></div>
         </Col>
          <Col span="12">
           <h3>院系数据</h3>
             <div class="total-canvas" id="c2"></div>
         </Col>
     </Row>
   </div>
 </div>
</template>

<script>
import SubTitle from "../components/SubTitle";

export default {
    data() {
        return {
            totalPerson: 2000
        };
    },
    beforeMount() {},
    mounted() {
        this.$http.get(`/admin/showCount/getTotalCount`).then(res => {
            console.log("/admin/ showCount/getTotalCount", res.data);
            if (res.data.status == 200) {
                let { userCount, passUserCount } = res.data.data;
                this.renderTotal(userCount, passUserCount);
            }
        });

        this.$http
            .get(`/admin/showCount/getCountByParentId?deptId=-1&deptType=1`)
            .then(res => {
                console.log("/admin/ showCount/getTotalCount", res.data);
                if (res.data.status == 200) {
                  this.renderPerAcademy(res.data.data)
                }
            });
    },
    components: {
        SubTitle
    },
    methods: {
        renderTotal(total, pass) {
            let passPercent = parseFloat(((pass/total)).toFixed(3));
            let remainPercent = 1-passPercent
            var data = [
                {
                    item: "通过",
                    count: pass,
                    percent:passPercent
                },
                {
                    item: "未通过",
                    count: total - pass,
                    percent: remainPercent
                }
            ];
            var chart = new window.G2.Chart({
                container: "c1",
                forceFit: true,
                height: 600,
                animate: false
            });
            chart.source(data, {
                percent: {
                    formatter: function formatter(val) {
                        val = val * 100 + "%";
                        return val;
                    }
                }
            });
            chart.coord("theta", {
                radius: 0.75,
                innerRadius: 0.6
            });
            chart.tooltip({
                showTitle: false,
                itemTpl:
                    '<li><span style="background-color:{color};" class="g2-tooltip-marker"></span>{name}: {value}</li>'
            });
            // 辅助文本
            chart.guide().html({
                position: ["50%", "50%"],
                html: `<div style="color:#8c8c8c;font-size: 14px;text-align: center;width: 10em;">学生总数<br><span style="color:#8c8c8c;font-size:20px">${total}</span>人</div>`,
                alignX: "middle",
                alignY: "middle"
            });
            var interval = chart
                .intervalStack()
                .position("percent")
                .color("item")
                .label("percent", {
                    formatter: function formatter(val, item) {
                        return item.point.item + ": " + val;
                    }
                })
                .tooltip("item*percent", function(item, percent) {
                    percent = percent * 100 + "%";
                    return {
                        name: item,
                        value: percent
                    };
                })
                .style({
                    lineWidth: 1,
                    stroke: "#fff"
                });
            chart.render();
            interval.setSelected(data[0]);
        },
        renderPerAcademy(dataArr) {
          let data = [{
             name: "总数",
          },{
            name: "通过",
          }];
          let fields = []
          dataArr.forEach(item=>{
            fields.push(item.deptName)
            data[0][item.deptName] = item.userCount
            data[1][item.deptName] = item.passUserCount
          })
          console.log('fileds',fields)

            var ds = new window.DataSet();
            var dv = ds.createView().source(data);
            dv.transform({
                type: "fold",
                fields:fields, // 展开字段集
                key: "院系", // key字段
                value: "通过情况" // value字段
            });

            var chart = new window.G2.Chart({
                container: "c2",
                forceFit: true,
                height: 600
            });
            chart.source(dv);
            chart
                .intervalStack()
                .position("院系*通过情况")
                .color("name");
            chart.render();
        }
    }
};
</script>

