import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import iView from 'iview';

import axios from 'axios'
import VueAxios from 'vue-axios'

import storage from './utils/storage'

import 'iview/dist/styles/iview.css';

if (process.env.NODE_ENV==='development') {

  axios.defaults.baseURL = 'http://192.168.0.121:8180'
  axios.defaults.baseURL = 'http://118.25.96.162:8080'

} else {
 axios.defaults.baseURL = 'http://118.25.96.162:8080'
}
axios.defaults.timeout = 10000;
axios.defaults.withCredentials = true;



router.beforeEach((to, from, next) => {

  if (to.meta.requireAuth) { // 判断该路由是否需要登录权限
    let userName = window.sessionStorage.getItem('userName') || store.state.userInfo.userName;
    if (userName) { // 通过vuex state获取当前的token是否存在
      next();
    } else {
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        } // 将跳转的路由path作为参数，登录成功后跳转到该路由
      })
    }
  } else {
    next();
  }
})

axios.interceptors.response.use(function (response) {
  // Do something with response data
  console.log('interceptors res')
  if (response.status===200 &&response.data.status==401) {
    console.log('鉴权失败')
    router.push('/login')
  }
  return response;
}, function (error) {
  // Do something with response error
  return Promise.reject(error);
});

Vue.use(VueAxios, axios)

Vue.use(iView);

Vue.config.productionTip = false

new Vue({
  router,
  store,
  storage,
  render: h => h(App)
}).$mount('#app')
