
// 用来读写sessionstorage和localstorage的工具


class Storage {
  constructor(){
    this.ss = window.sessionStorage;
    this.ls = window.localStorage;
  }

  setSS(name,value){
    this.ss.setItem(name,value)
  }

  getSS(name) {
    return this.ss.getItem(name)
  }

  setLS(name,value){
    this.ls.setItem(name,value)
  }
  
  getLS(name) {
    return this.ls.getItem(name)
  }

  deleteSS(name){
    this.ss.removeItem(name)
  }

  deleteLS(name) {
    this.ls.removeItem(name)
  }

  clearSS(){
    this.ss.clear()
  }

  clearLS(){
    this.ls.clear()
  }

  dealUserLogin(userInfo){
    let {name,level,id} = userInfo
    this.setSS('name',name);
    this.setSS('level',level);
    this.setSS('id',id);
  }

  dealUserLoginOut(){
    this.clearSS()
  }

}

export default new Storage();