<style scoped>
.page-box {
  background: linear-gradient(30deg,#2d8cf0,white);
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.login-box {
  width: 400px;
  margin: 80px auto 0 auto;
}

.login-title {
  margin-top: 100px;
}

.form-box {
  width: 80%;
  margin: 30px auto;
}
</style>
<template>
 <div class="page-box">
   <h1 class="login-title">河南师范大学安全培训考试管理系统</h1>
   <Card class="login-box">
     <h3>登录</h3>
        <div style="text-align:center" class="form-box">
              
             <Form ref="formInline" :model="formInline" :rules="ruleInline" >
                <FormItem prop="user">
                    <Input type="text" v-model="formInline.user" placeholder="用户名">
                        <Icon type="ios-person-outline" slot="prepend"></Icon>
                    </Input>
                </FormItem>
                <FormItem prop="password">
                    <Input type="password" v-model="formInline.password" placeholder="密码">
                        <Icon type="ios-lock-outline" slot="prepend"></Icon>
                    </Input>
                </FormItem>
                <FormItem>
                    <Button type="primary" @click="handleSubmit('formInline')">登陆</Button>
                    <Button type="warning" style="margin-left:40px;" @click="handleSubmit('formInline')">重置</Button>
                </FormItem>
            </Form>
        </div>
    </Card>
 </div>
</template>

<script>
export default {
  data() {
    return {
      formInline: {
        user: "",
        password: ""
      },
      ruleInline: {
        user: [
          {
            required: true,
            message: "请输入用户名",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: "请输入密码",
            trigger: "blur"
          },
          {
            type: "string",
            min: 6,
            message: "密码至少6位",
            trigger: "blur"
          }
        ]
      }
    };
  },
  components: {},
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          
          this.$http.post('/admin/login',{
            userName:this.formInline.user,
            password:this.formInline.password
          }).then(res=>{
            console.log('admin login',res.data)
            if (res.data.status == 200) {
              let data= res.data.data
              let userInfo = {
                userName:data.userName,
                userType:data.userType
              }
              this.$Message.success("登陆成功!");
              this.$store.dispatch('UserLogin',userInfo)
              this.$router.push('/index/')
            } else {
              this.$Message.error("用户名或密码错误")
            }
          })
          
        } else {
          this.$Message.error("请正确输入");
        }
      });
    }
  }
};
</script>

