<style scoped>
.page-box {
}
.table-box {
    padding: 5px 20px;
}
.btn-box {
    text-align: left;
    margin: 5px auto;
}
.input-item {
    margin-bottom: 10px;
}
.input-item-title {
    font-weight: bold;
    margin-bottom: 3px;
}
.detail-title {
  font-weight: bold;
  text-align: center;
}
.detail-box {
  text-align: center;
}

</style>
<template>
  <div class="page-box">
    <sub-title title="成绩查询" />

    <div class="table-box">
      <Row>
        <Col class="btn-box" span="24">
        <ButtonGroup>
          <Button type="primary" @click="openSearchModal">按条件查询</Button>
          <!-- <Button type="error" @click="deleteLotOfStus">批量删除</Button> -->
          <Button type="warning" @click="cleanSearch">清除查询</Button>
        </ButtonGroup>
        
        </Col>
      </Row>
      <Table @on-selection-change="tableSelectChange" :loading="tableLoading" @on-row-dblclick="clickRow" size="small"
        border :columns="columnsData" :data="tableData"></Table>
      <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
          <Page :total="tableTotal" :page-size="30" :current="currentPage" @on-change="changePage"></Page>
        </div>
      </div>
    </div>
    <Modal v-model="searchModal">
      <p slot="header">
        <span>按条件查询</span>
      </p>
      <Tabs v-model="searchInfo.recentTab">
        <TabPane label="按班级" name="tab1">
          <Cascader transfer style="width: 200px" :data="cascaderData" v-model="searchInfo.classDeptArr"></Cascader>
        </TabPane>
        <TabPane label="按年级" name="tab2">
          <Cascader transfer style="width: 200px" :data="gradeCascader" v-model="searchInfo.gradeDeptArr"></Cascader>
        </TabPane>
        <TabPane label="按院系" name="tab3">
          <Cascader transfer style="width: 200px" :data="academyCascader" v-model="searchInfo.academyDeptArr"></Cascader>
        </TabPane>
        <TabPane label="关键字" name="tab4">
          关键词：<Input v-model="searchInfo.keyWord" placeholder="搜索关键词" style="width: 300px" />
        </TabPane>
      </Tabs>
      <div slot="footer">
        <Button type="primary" @click="confirmSearch">搜索</Button>
      </div>
    </Modal>

    <Modal v-model="detailModal" width="600">
      <p slot="header">
        <span>成绩详情-{{detailName}}-{{detailNum}}</span>
      </p>
      <div class="detail-box">
        <Row class="detail-title">
            <Col span="6">
              考试时间
            </Col>
            <Col span="6">
              结果
            </Col>
            <Col span="6">
              分数
            </Col>
            <Col span="6">
              错题数量
            </Col>
        </Row>
        <Row v-for="item in detailArr" :key="item.reportId">
            <Col span="6">
              {{item.time}}
            </Col>
            <Col span="6">
              {{item.result}}
            </Col>
            <Col span="6">
              {{item.score}}
            </Col>
            <Col span="6">
              {{item.errorCount}}
            </Col>
        </Row>
      </div>
    </Modal>
  </div>

</template>

<script>
import SubTitle from "../components/SubTitle";
import {secondsToDateString} from '../utils/dateTools.js'

export default {
    data() {
        return {
            detailName:'',
            detailNum:'',
            detailArr:[
              { 
                reportId:1,
                time:'啊打发法',
                result:'过',
                score:12,
                errorCount:1
              }

            ],
            searchModal: false,
            tableLoading: false,
            currentPage: 1,
            tableTotal: 0,
            detailModal: false,
            cascaderData: [],
            gradeCascader: [],
            academyCascader: [],
            tableMode: "default",
            searchInfo: {
                confirmTab:'tab1',
                recentTab: "tab1",
                classDeptArr: [],
                gradeDeptArr: [],
                academyDeptArr: [],
                keyWord: ""
            },
            columnsData: [
                {
                    title: "学号",
                    key: "stuId"
                },
                {
                    title: "姓名",
                    key: "name"
                },
                
                {
                    title: "性别",
                    width: 80,
                    key: "sexStr"
                },
                {
                    title: "院系/年级/班级",
                    key: "dept"
                },
                {
                  title:"考试状态",
                  key:'status'
                },{
                  title:'考试次数',
                  key:"count"
                },{
                  title:'最高分',
                  key:'max'
                }
            ],
            tableData: []
        };
    },
    async beforeMount() {
        if (await this.getDeptInfo()) {
            this.getDeptTree();
        }
    },
    components: {
        SubTitle
    },
    methods: {
        cleanSearch() {
            this.tableData = []
        },
        openSearchModal() {
            this.searchModal = true;
        },
        confirmSearch() {
            this.getSearchResult(1,this.searchInfo.recentTab);
        },
        getSearchResult(page,tab) {
            let deptType, deptId, keyWord;
            switch (tab) {
                case "tab1":
                    if (this.searchInfo.classDeptArr.length > 0) {
                        deptType = 5;
                        deptId = this.searchInfo.classDeptArr[2];
                    }
                    break;
                case "tab2":
                    if (this.searchInfo.gradeDeptArr.length > 0) {
                        deptType = 3;
                        deptId = this.searchInfo.gradeDeptArr[1];
                    }
                    break;
                case "tab3":
                    if (this.searchInfo.academyDeptArr.length > 0) {
                        deptType = 1;
                        deptId = this.searchInfo.academyDeptArr[0];
                    }
                    break;
                case "tab4":
                    keyWord = this.searchInfo.keyWord;
                    break;
                default:
                    break;
            }
            this.tableMode = "search";
            this.tableLoading = true;
            this.searchInfo.confirmTab = tab;
            this.$http
                .post("/admin/examRule/getReport", {
                    deptType,
                    deptId,
                    userType: 5,
                    keyWord,
                    page,
                    row: 30
                })
                .then(res => {
                    if (res.data.status == 200) {
                        this.tableLoading = false;
                        let resultArr = res.data.data.resultList;
                        this.tableTotal = res.data.data.count;
                        console.log('resultArr',resultArr)
                        this.formatTableData(resultArr);
                        this.searchModal = false;
                    } else {
                        this.$Message.error("查询错误");
                    }
                });
        },
        formatCascaderData() {
            this.cascaderData = [];
        },
        getDeptTree() {
            this.$http.get("/admin/dept/getDeptNodeTree").then(res => {
                console.log("dept info", res.data);
                if (res.data.status == 200) {
                    let deptTree = res.data.data;
                    console.log("dept tree", deptTree);
                    let newArr1 = JSON.parse(JSON.stringify(deptTree));
                    let newArr2 = JSON.parse(JSON.stringify(deptTree));
                    let newArr3 = JSON.parse(JSON.stringify(deptTree));
                     newArr1 = moveUnToLast(newArr1)
                     newArr2 = moveUnToLast(newArr2)
                     newArr3 = moveUnToLast(newArr3)
                    this.cascaderData = traceDeptTree(newArr1, "normal");
                    this.gradeCascader = traceDeptTree(newArr2, "grade");
                    this.academyCascader = traceDeptTree(newArr3, "academy");
                } else {
                    this.$Message.error({
                        content: "网络错误"
                    });
                }
            });
        },
        getDeptInfo() {
            return new Promise((resolve, reject) => {
                this.deptMap = Object.create(null);
                this.$http.get("/admin/dept/getAllDept").then(res => {
                    console.log("dept info", res.data);
                    if (res.data.status == 200) {
                        let deptArr = res.data.data;
                        deptArr.forEach(item => {
                            this.deptMap[item.deptId] = item.deptName;
                        });
                        resolve(true);
                    } else {
                        this.$Message.error({
                            content: "网络错误"
                        });
                        reject(false);
                    }
                });
            });
        },
        formatTableData(dataArr) {
            this.tableData = [];
            dataArr.forEach(item => {
                let temp = {};
                temp.name = item.realName;
                temp.sex = item.sex;
                temp.sexStr = item.sex > 0 ? "女" : "男";
                temp.userId = item.userId;
                temp.stuId = item.userNum;
                temp.academy = this.deptMap[item.academyId];
                temp.grade = this.deptMap[item.gradeId];
                temp.class = this.deptMap[item.classId];
                temp.dept = `${this.deptMap[item.academyId]}/${this.deptMap[item.gradeId]}/${this.deptMap[item.classId]}`
                temp.academyId = item.academyId;
                temp.gradeId = item.gradeId;
                temp.classId = item.classId;
                temp.status = item.resultState>0?'通过':"未通过";
                temp.cellClassName ={}
                temp.cellClassName.status = item.resultState>0?'pass-cell':'nopass-cell';
                temp.count = item.examCount;
                temp.max = item.maxScore;
                temp.reg = "统一导入";
                this.tableData.push(temp);
            });
        },
        changePage(index) {
            // 根据table 的模式，选择不同的页面切换方法
            // 分展示模式和搜索模式
            this.currentPage = index;
            console.log("页面切换了", index, this.currentPage);
            if (this.tableMode === "default") {
                this.getAllStusPerPage(this.currentPage);
            } else if (this.tableMode === "search") {
                this.getSearchResult(this.currentPage,this.searchInfo.confirmTab)
            }
        },
        clickRow(rowData) {
            console.log(rowData)
            let userId =rowData.userId;
            this.detailName = rowData.name
            this.detailNum = rowData.stuId
            this.$http.get(`/admin/examRule/getReportByStudentId?userId=${userId}`).then(res=>{
              console.log('/admin/ examRule / getReportByStudentId',res.data)
              if (res.data.status == 200) {
                let resultArr = res.data.data
                this.formatDetail(resultArr)
                this.detailModal = true;

              }else {
                this.$Message.error('网络错误')
              }
              
            })
            

        },
        formatDetail(resultArr){
          this.detailArr = [];
          resultArr.forEach(item=>{
            let temp = {}
            temp.reportId = item.reportId
            temp.time = secondsToDateString(item.startTime)
            temp.result = item.resultState>0?'通过':'未通过'
            temp.score = item.userScore
            temp.errorCount = item.errorCount
            this.detailArr.push(temp)

          })
        },
        resetCrtBtn() {
            this.inputDis = true;
            this.crtBtnType = "warning";
            this.crtBtnTitle = "修改";
        },
        refreshRecentTable() {
            if (this.tableMode === "default") {
                this.getAllStusPerPage(this.currentPage);
            } else if (this.tableMode === "search") {
                this.getSearchResult(this.currentPage,this.searchInfo.confirmTab)
            }
        },
        tableSelectChange(selection) {
            console.log("selection", selection);
            this.selectionArr = selection;
        }
    }
};
function moveUnToLast(treeArr) {
  let newTreeArr = treeArr.slice(0);
  let unIndex = newTreeArr.findIndex(item => {
    return item.deptType === 0;
  });
  let unItem = newTreeArr[unIndex];
  newTreeArr.splice(unIndex, 1);
  newTreeArr.push(unItem);
  return newTreeArr;
}

function traceDeptTree(treeArr, traceType) {
    treeArr.forEach(item => {
        switch (traceType) {
            case "normal":
                break;
            case "grade":
                if (item.deptType == 3) {
                    delete item.children;
                }
                break;
            case "academy":
                if (item.deptType == 1) {
                    delete item.children;
                }
                break;

            default:
                break;
        }
        item.label = item.deptName;
        item.value = item.deptId;
        if (item.children && item.children.length > 0) {
            traceDeptTree(item.children, traceType);
        } else {
            delete item.children;
        }
    });

    return treeArr;
}
</script>