<style scoped>
.set-box {
  min-height: 340px;
}

.form {
  width: 80%;
  margin: 0 auto;
  margin-top: 20px;
}
.setting-type-title {
  font-size: 14px;
  font-weight: bold;
  margin-top: 5px;
  margin-bottom: 5px;
}

.type-row {
  margin-top: 5px;
  margin-bottom: 5px;
}

.compute-btn {
  margin-top: 10px;
}
</style>
<template>
 <div>
   <Row>
     <Col span="24">
     <sub-title title="考试设置" />
      </Col>
   </Row>
   <Alert type="warning">考试总分100分，提交前请-<b>计算总分</b>-为100分，考试岂止时间是考生能够参加正式考试的一个日期范围</Alert>
    <Row>
      <Col class="set-box" span="14">
        <h2>试题设置</h2>
        <div class="form">
          <div>
            <Row class="setting-type-title">
              <Col span="5">
                   题型名称
                 </Col>
                 <Col span="5">
                   题型总数
                 </Col>
                 <Col span="5">
                   考试题数
                 </Col>
                 <Col span="5">
                   每题分值
                 </Col>
                 <Col span="4">
                   题型总分
                 </Col>
            </Row>
             <Row class="type-row" v-for="item in typeMap" :key="item.index">
                 <Col span="5">
                   {{item.typeName}}
                 </Col>
                 <Col span="5">
                    {{item.count}}
                 </Col>
                 <Col span="5">
                    <InputNumber :max="item.count" :min="0" v-model="item.selectCount"></InputNumber>
                 </Col>
                 <Col span="5">
                   <InputNumber :max="100" :min="0" v-model="item.perScore"></InputNumber>
                 </Col>
                 <Col span="4">
                   {{item.perTotal}}
                 </Col>
             </Row>
             
          </div>
          <div class="compute-btn">
            <Row>
                 <Col span="12">
                   当前总分：<b style="color:red;">{{totalScore}}</b>
                 </Col>
                 <Col span="12">
                   <Button size="small" type="primary" @click="computeScore">计算总分</Button>
                 </Col>
            </Row>
          </div>
        </div>
      </Col>
      <Col class="set-box" span="10">
        <h2>其他设置</h2>
        <div class="form">
        <Form :model="setting">
          <FormItem label="考试时间">
            <InputNumber :max="180" :min="10" :step="10" v-model="setting.time" :editable="true"></InputNumber> 分钟
          </FormItem>
          <FormItem label="通过分数">
            <InputNumber :max="100" :min="10" :step="5" v-model="setting.passScore" :editable="true"></InputNumber> 分
          </FormItem>
          <FormItem label="当前岂止设置">
            {{setting.disStartTime}} -- {{setting.disEndTime}}
          </FormItem>
          <FormItem label="选择起止时间">
            <DatePicker split-panels @on-change="dateChange" size="small" type="daterange" placement="top-end" placeholder="请选择" style="width: 200px"></DatePicker>
          </FormItem>
        </Form>
        </div>
      </Col>
    </Row>
    <Row>
      <Col :push="18" span="6">

       <Button type="success" @click="confirmChange">确定</Button>
        <Button type="warning" style="margin-left:15px;" @click="resetInput">重置</Button>
      </Col>
    </Row>
 </div>
</template>

<script>
import {date2SecondsStr,secondsStrToDate} from '../utils/dateTools.js'
import SubTitle from "../components/SubTitle";
export default {
  data() {
    return {
      totalScore: 0,
      setting: {
        singleNum: 10,
        time: 30,
        startTime:'',
        endTime:'',
        passScore: 60,
        disStartTime:'',
        disEndTime:''
      },
      typeMap: {},
      quesTypeArr: [],
      typeSettingArr: [],
      timeRangeArr:['','']
    };
  },
  beforeMount() {
    this.getQuesType().then(res => {
      console.log("--------------->", res.data);
      if (res.data.status == 200) {
        this.formatTypeArr(res.data.data);
        this.getRecentSetting();
      } else {
        this.$Message.error("获取配置失败");
      }
    });
  },
  components: {
    SubTitle
  },
  methods: {
    formatTypeArr(typeArr) {
      this.quesTypeArr = [];
      this.typeMap = {};
      typeArr.forEach(item => {
        if (item.count > 0) {
          let temp = {};
          let typeId = item.typeId;
          this.typeMap[typeId] = {};
          this.typeMap[typeId].typeId = item.typeId;
          this.typeMap[typeId].typeName = item.typeName;
          this.typeMap[typeId].count = item.count;
          temp.typeName = item.typeName;
          temp.count = item.count;
          temp.typeId = item.typeId;
          this.quesTypeArr.push(temp);
        }
      });
    },
    computeScore() {
      let total = 0;
      for (let key in this.typeMap) {
        if (this.typeMap[key].selectCount > 0) {
          if (this.typeMap[key].perScore > 0) {
             this.typeMap[key].perTotal=this.typeMap[key].selectCount * this.typeMap[key].perScore
            total += this.typeMap[key].selectCount * this.typeMap[key].perScore;
          } else {
            this.$Message.error("每题分数存在0分设置");
            return
          }
        }
      }
      this.totalScore = total
      if (total!==100) {
        this.$Message.error('提醒！总分不为100分')
      }
    },
    confirmChange(){
      // 先校验分数
      let total = 0;
      let upTypeJsonArr = [];
      for (let key in this.typeMap) {
        if (this.typeMap[key].selectCount > 0) {
          if (this.typeMap[key].perScore > 0) {
             this.typeMap[key].perTotal=this.typeMap[key].selectCount * this.typeMap[key].perScore
            total += this.typeMap[key].selectCount * this.typeMap[key].perScore;
            let temp = {}
            temp.questionTypeId = this.typeMap[key].typeId;
            temp.num = this.typeMap[key].selectCount;
            temp.score = this.typeMap[key].perScore;
            upTypeJsonArr.push(temp)
          } else {
            this.$Message.error("每题分数存在0分设置");
            return
          }
        }
      }
      this.totalScore = total
      if (total!==100) {
        this.$Message.error('提醒！总分不为100分')
        return 
      } else {
          // 校验时间 
          if (this.setting.time>9 && this.setting.time<181) {
            // 校验通过分数
            if (this.setting.passScore>0 && this.setting.passScore<101) {
              if (this.timeRangeArr[0].length>0) {
                //说明修改了岂止时间
                let start = this.timeRangeArr[0];
                let end = this.timeRangeArr[1];
                let sDate = new Date(start);
                let eDate = new Date(end);
                
                let sString = date2SecondsStr(sDate);
                let eString = date2SecondsStr(eDate);
                console.log(sDate,eDate);
                console.log(sString,eString);
                this.$http.post('/admin/examRule/saveExamRule',{
                  examPassScore:this.setting.passScore,
                  examTime:this.setting.time,
                  examTotal:100,
                  examJson:JSON.stringify(upTypeJsonArr),
                  startTime:sString,
                  endTime:eString
                }).then(res=>{
                    if (res.data.status == 200) {
                      this.$Message.success('设置成功')
                      this.resetInput()
                    } else {
                      this.$Message.error('设置失败')
                    }
                })
              } else {
                if (this.setting.startTime && this.setting.endTime) {
                  this.$http.post('/admin/examRule/saveExamRule',{
                  examPassScore:this.setting.passScore,
                  examTime:this.setting.time,
                  examTotal:100,
                  examJson:JSON.stringify(upTypeJsonArr)
                }).then(res=>{
                    if (res.data.status == 200) {
                      this.$Message.success('设置成功')
                      this.resetInput()
                    } else {
                      this.$Message.error('设置失败')
                    }
                    
                })
                } else {
                  this.$Message.error('请先设置时间')
                }
                

              }
            } else {
              this.$Message.error('通过分数设置错误')
              return 
            }

          } else {
            this.$Message.error('考试时间设置错误，范围10-180分钟')
            return 
          }
      }
    },
    mergeSettingIntoArr(typeSettingArr) {
      if (typeSettingArr.length>0) {
        for (let key in this.typeMap) {
        typeSettingArr.forEach(item => {
          if (this.typeMap[key].typeId === item.questionTypeId) {
            this.typeMap[key].selectCount = item.num;
            this.typeMap[key].perScore = item.score;
            this.typeMap[key].perTotal = item.num * item.score
          }
        });
      }
      }
      
      for (let key in this.typeMap) {
        if (!this.typeMap[key].selectCount) {
          this.typeMap[key].selectCount =0;
          this.typeMap[key].perScore = 0;
           this.typeMap[key].perTotal = 0;
        }
      }
      this.$forceUpdate()
      this.computeScore()
    },
    getQuesType() {
      return this.$http.get("/admin/question/getQuestionType");
    },
    getRecentSetting() {
      this.$http.get("/admin/examRule/getExamRule").then(res => {
        console.log("/admin/examRule/getExamRule", res.data);
        if (res.data.status == 200) {
          let data = res.data.data;
          if (data) {
            this.setting.time = parseInt(data.examTime);
          this.setting.passScore = data.examPassScore;
          this.setting.startTime = data.startTime;
          this.setting.endTime = data.endTime;
          this.setting.startDate = secondsStrToDate(data.startTime);
          this.setting.endDate = secondsStrToDate(data.endTime);
          this.setting.disStartTime = this.setting.startDate.toLocaleDateString();
          this.setting.disEndTime = this.setting.endDate.toLocaleDateString();
          this.typeSettingArr = JSON.parse(data.examJson);
          this.mergeSettingIntoArr(this.typeSettingArr);
          } else {
            this.setting.startTime = null;
            this.setting.endTime = null;
            this.setting.disStartTime = "未设置";
          this.setting.disEndTime = '未设置';
            this.typeSettingArr = []
            this.mergeSettingIntoArr(this.typeSettingArr);
          }
          

        } else {
          this.$Message.error("网络错误");
        }
      });
    },
    resetInput() {
      this.getRecentSetting();
    },
    dateChange(arr){
      console.log('changed')
      this.timeRangeArr[0] =arr[0];
      this.timeRangeArr[1] =arr[1];
      console.log('now',this.timeRangeArr)
    }
  }
};
</script>

