<style scoped>
.page-box {
  text-align: left;
}
.table-box {
  padding: 5px 20px;
}
.btn-box {
  text-align: left;
  margin-bottom: 10px;
}
.modal-question {
  padding: 5px;
  min-height: 50px;
  font-weight: bold;
  font-size: 14px;
}
.modal-answer-item {
  min-height: 80px;
  padding: 5px;
}
.right-answer {
  background-color: #19be6b;
  border-radius: 5px;
}

.img-box {
  text-align: center;
}

.question-img {
  width: 360px;
  padding: 10px;
}
</style>
<template>
 <div class="page-box">
   <sub-title title="试题管理" />
   <Alert type="warning" closable>先选择题目按分类查询，双击修改相应条目</Alert>
   <div class="table-box">
     <Row>
     <Col class="btn-box" span="18">
          <ButtonGroup>
              <Button type="primary" @click="addNewQues">新增题目</Button>
              <Button type="error" @click="deleteLotOfQues">批量删除</Button>     
          </ButtonGroup>
      </Col>
      <Col span="6">
        <Select placeholder="请选择题目分类" @on-change="changeCate" v-model="displayDataInfo.displayCate" style="width:200px">
            <Option v-for="item in tableCategoryList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        
      </Col>
   </Row>
   <Table @on-selection-change="tableSelectChange" no-data-text="暂无数据" :loading="tableLoading" :highlight-row="true" @on-row-dblclick="clickRow" size="small" border :columns="columnsData" :data="tableData"></Table>
     <div style="margin: 10px;overflow: hidden">
          <div style="float: right;">
              <Page :total="displayDataInfo.total" :page-size="30" :current="currentPage" @on-change="changePage"></Page>
          </div>
      </div>
    <Modal v-model="dblclickModal" width="500">
        <p slot="header">
            <span>{{modalTitle}}</span>
        </p>
        <div>
            <Row>
                <Col class="modal-question" span="24">
                  <p>题目：</p>
                  <Input :disabled="inputDis" v-model="clickRecentQ.question" type="textarea" placeholder="题目题干" />
                </Col>
                <Col v-if="clickRecentQ.isImg" class="img-box" span="24">
                    <img class="question-img" :src="clickRecentQ.url" alt="img" />
                </Col>
            </Row>
            <Row>
                <Col class="modal-answer-item" span="12">
                  <p>答案A:</p>
                  <Input :disabled="inputDis" v-model="clickRecentQ.answer1" type="textarea" placeholder="答案A" />
                </Col>
                <Col class="modal-answer-item" span="12">
                  <p>答案B:</p>
                  <Input :disabled="inputDis" v-model="clickRecentQ.answer2" type="textarea" placeholder="答案B" />
                </Col>
            </Row>
            <Row>
                <Col class="modal-answer-item" span="12">
                  <p>答案C:</p>
                  <Input :disabled="inputDis" v-model="clickRecentQ.answer3" type="textarea" placeholder="答案C" />
                </Col>
                <Col class="modal-answer-item" span="12">
                  <p>答案D:</p>
                  <Input :disabled="inputDis" v-model="clickRecentQ.answer4" type="textarea" placeholder="答案D" />
                </Col>
            </Row>
            <Row>
                <Col class="modal-answer-item" span="12">
                  <p>正确答案:</p>
                  <Select :disabled="inputDis" v-model="clickRecentQ.right" style="width:200px">
                      <Option v-for="item in rightList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                  </Select>
                </Col>
                <Col class="modal-answer-item" span="12">
                  <p>题目分类:</p>
                  <Select :disabled="inputDis" v-model="clickRecentQ.categoryId" style="width:200px">
                      <Option v-for="item in categoryList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                  </Select>
                </Col>
            </Row>
        </div>
        <div slot="footer">
            <Button v-if="clickRecentQ.isImg" type="info">更换图片</Button>
            <Button type="error" v-if="!isAddNew" :loading="delete_loading" @click="deleteRecent">删除</Button>
            <Button :type="crtBtnType" :loading="correct_loading" @click="correctRecent">{{crtBtnTitle}}</Button>
        </div>
    </Modal>
   </div>
 </div>
</template>

<script>
/**
 * { 
    questionId:1,
    categoryName:'数学题',
    categoryId: 1,
    question:
      "这是耨一个问题的体感啊快点哈可是你查看节哀顺变节哀顺变就卡巴斯基啊",
    isImg: "不是图片题",
    questionForm:0,
    url:'',
    answer1: "罚款就放假的时间你叫撒房间索南达杰啊",
    answer2: "啊看见回到家啊可是低价按时",
    answer3: "达到hi啊滴哈死的好次啊时间的慈爱",
    answer4: "j你覅啊送积分哦啊睡觉分类科目撒口父母开",
    right: 3
  }
 */
import SubTitle from "../components/SubTitle";
export default {
  data() {
    return {
      modalTitle:'题目详情',
      isAddNew: true,
      selectionArr: [],
      crtBtnType: "warning",
      crtBtnTitle: "修改",
      inputDis: false,
      currentPage: 1,
      tableLoading: false,
      displayDataInfo: {
        displayCate: null,
        total: 0
      },
      delete_loading: false,
      correct_loading: false,
      tableCategoryList: [],
      categoryList: [],
      rightList: [
        {
          label: "A",
          value: 1
        },
        {
          label: "B",
          value: 2
        },
        {
          label: "C",
          value: 3
        },
        {
          label: "D",
          value: 4
        }
      ],
      clickRecentQ: {
        questionId: 1,
        categoryId: 1,
        categoryName: "数学题",
        isImg: true,
        questionForm: 1,
        question:
          "这是耨一个问题的体感啊快点哈可是你查看节哀顺变节哀顺变就卡巴斯基啊",
        answer1: "罚款就放假的时间你叫撒房间索南达杰啊",
        url: "http://www.baidu.com/img/bd_logo1.png",
        answer2: "啊看见回到家啊可是低价按时",
        answer3: "达到hi啊滴哈死的好次啊时间的慈爱",
        answer4: "j你覅啊送积分哦啊睡觉分类科目撒口父母开",
        right: 3
      },
      dblclickModal: false,
      category: [],
      columnsData: [
        {
          type: "selection",
          width: 50,
          align: "center"
        },
        {
          title: "分类",
          width: 100,
          key: "categoryName"
        },
        {
          title: "题目",
          width: 200,
          key: "question"
        },
        {
          title: "是否图片题",
          key: "isImg"
        },
        {
          title: "答案A",
          key: "answer1"
        },
        {
          title: "答案B",
          key: "answer2"
        },
        {
          title: "答案C",
          key: "answer3"
        },
        {
          title: "答案D",
          key: "answer4"
        },
        {
          title: "正确答案",
          key: "right"
        }
      ],
      tableData: []
    };
  },
  beforeMount() {
    this.getQuesType();
  },

  components: {
    SubTitle
  },
  methods: {
    changeCate(cateId) {
      console.log("select change", cateId);
      this.tableLoading = true;
      this.displayDataInfo.displayCate = cateId;
      this.$http
        .get(
          `/admin/question/getQuestionByType?questionTypeId=${cateId}&page=1&row=30`
        )
        .then(res => {
          console.log("/admin/question/getQuestionByType", res.data);
          this.tableLoading = false;
          if (res.data.status == 200) {
            if (res.data.data.count > 0) {
              let quesArr = res.data.data.questionList;
              
              this.formatTable(quesArr);
              this.displayDataInfo.total = res.data.data.count;
              this.currentPage = 1;
            } else {
              this.tableData = [];
            }
          }
        });
    },
    getQuesType() {
      this.$http.get("/admin/question/getQuestionType").then(res => {
        console.log("获取题目分类", res.data);
        if (res.data.status == 200) {
          let cateArr = res.data.data;
          this.formatCateMap(cateArr);
          this.formatCateList(cateArr);
          this.formatTableCateList(cateArr);
        }
      });
    },
    setRecentQ(rowData) {
      this.isAddNew = false;
      this.clickRecentQ = {};
      this.clickRecentQ.question = rowData.question;
      this.clickRecentQ.answer1 = rowData.answer1;
      this.clickRecentQ.answer2 = rowData.answer2;
      this.clickRecentQ.answer3 = rowData.answer3;
      this.clickRecentQ.answer4 = rowData.answer4;
      this.clickRecentQ.right = rowData.right;
      this.clickRecentQ.categoryId = rowData.categoryId;
      this.clickRecentQ.categoryName = rowData.categoryName;
      this.clickRecentQ.questionForm = rowData.questionForm;
      this.clickRecentQ.questionId = rowData.questionId;
      this.inputDis = true;
      this.resetCrtBtn();
      if (rowData.questionForm) {
        this.clickRecentQ.isImg = true;
        this.clickRecentQ.url = rowData.url;
      } else {
        this.clickRecentQ.isImg = false;
        this.clickRecentQ.url = "";
      }
    },
    clickRow(data) {
      console.log("double click row", data);
      this.setRecentQ(data);
      this.dblclickModal = true;
      this.modalTitle = '题目详情'
    },
    deleteRecent() {
      console.log("删除当前试题");
      this.$Modal.confirm({
        title: "删除确认",
        content: "请确认是否删除本题？",
        onOk: () => {
          let qId = this.clickRecentQ.questionId;
          let arr = [];
          arr.push(qId);
          this.$http
            .post("/admin/question/deleteQuestionById", {
              questionIdsJson: JSON.stringify(arr)
            })
            .then(res => {
              if (res.data.status == 200) {
                this.$Message.success("删除成功");
                this.dblclickModal = false;
              } else {
                this.$Message.error("删除失败");
              }
            });
        }
      });
    },
    refreshTable() {
      let page = this.currentPage;
      this.getEachPage(page);
    },
    resetCrtBtn() {
      this.crtBtnType = "warning";
      this.crtBtnTitle = "修改";
    },
    correctRecent() {
      if (this.inputDis) {
        console.log("可以修改");
        this.inputDis = false;
        this.crtBtnType = "success";
        this.crtBtnTitle = "确定";
      } else {
        if (this.isAddNew) {
          let {
            question,
            questionForm,
            answer1,
            answer2,
            answer3,
            answer4,
            right,
            categoryId,
          } = this.clickRecentQ;
          if (question.length>0 && answer1.length>0 && answer2.length>0&&answer3.length>0 && answer4.length && right && categoryId) {
            console.log('param check success')
            this.$http.post('/admin/question/saveQuestion',{
              questionTypeId:categoryId,
              questionForm,
              questionTitle:question,
              optionA:answer1,
              optionB:answer2,
              optionC:answer3,
              optionD:answer4,
              answer:right
            }).then(res=>{
              console.log('/admin/question/saveQuestion--------------->',res.data);
              if (res.data.status == 200) {
                this.$Message.success('新建试题成功')
                this.dblclickModal = false;
              } else {
                this.$Message.error('新建试题失败')
              }
            })
          } else {
            this.$Message.error('请检查输入')
          }
        } else {
          let {
            question,
            answer1,
            answer2,
            answer3,
            answer4,
            right,
            categoryId,
            questionId
          } = this.clickRecentQ;
          this.$http
            .post("/admin/question/updateQuestion", {
              questionId,
              questionTypeId: categoryId,
              questionTitle: question,
              optionA: answer1,
              optionB: answer2,
              optionC: answer3,
              optionD: answer4,
              answer: right
            })
            .then(res => {
              console.log("/admin/question/updateQuestion", res.data);
              if (res.data.status == 200) {
                this.$Message.success({
                  content: "修改成功"
                });
                this.refreshTable();
                this.dblclickModal = false;
              } else {
                this.$Message.error({
                  content: "修改失败，请检查"
                });
              }
            });
        }
      }
    },
    formatTable(dataArr) {
      this.tableData = [];
      dataArr.forEach(item => {
        let temp = {};
        temp.question = item.questionTitle;
        temp.answer1 = item.optionA;
        temp.answer2 = item.optionB;
        temp.answer3 = item.optionC;
        temp.answer4 = item.optionD;
        temp.right = item.answer;
        temp.isImg = item.questionForm>0?'是':'否';
        temp.questionId = item.questionId;
        temp.categoryId = item.questionTypeId;
        temp.categoryName = item.questionTypeName;
        this.tableData.push(temp);
      });
    },

    formatCateMap(cateArr) {
      this.cateMap = Object.create(null);
      cateArr.forEach(item => {
        this.cateMap[item.typeId] = item.typeName;
      });
    },
    formatTableCateList(cateArr) {
      this.tableCategoryList = [];
      this.tableCategoryList.push({
        label: "全部",
        value: -1
      });
      cateArr.forEach(item => {
        let temp = {};
        temp.label = item.typeName;
        temp.value = item.typeId;
        this.tableCategoryList.push(temp);
      });
    },
    formatCateList(cateArr) {
      this.categoryList = [];
      cateArr.forEach(item => {
        let temp = {};
        temp.label = item.typeName;
        temp.value = item.typeId;
        this.categoryList.push(temp);
      });
    },

    changePage(page) {
      console.log("change page", page);
      this.currentPage = page
      this.getEachPage(page);
    },
    getEachPage(page) {
      let cateId = this.displayDataInfo.displayCate;
      this.$http
        .get(
          `/admin/question/getQuestionByType?questionTypeId=${cateId}&page=${page}&row=30`
        )
        .then(res => {
          console.log("/admin/question/getQuestionByType", res.data);
          this.tableLoading = false;
          if (res.data.status == 200) {
            if (res.data.data.count > 0) {
              this.displayDataInfo.total = res.data.data.count;
              let quesArr = res.data.data.questionList;
              this.formatTable(quesArr);
            } else {
              this.tableData = [];
            }
          }
        });
    },
    tableSelectChange(selection) {
      console.log("selection", selection);
      this.selectionArr = selection;
    },
    deleteLotOfQues() {
      if (this.selectionArr.length > 0) {
        let number = this.selectionArr.length;
        this.$Modal.confirm({
          title: "删除确认",
          content: `请确认是否删除选中的${number}条问题？`,
          onOk: () => {
            let arr = [];
            this.selectionArr.forEach(item => {
              arr.push(item.questionId);
            });

            this.$http
              .post("/admin/question/deleteQuestionById", {
                questionIdsJson: JSON.stringify(arr)
              })
              .then(res => {
                if (res.data.status == 200) {
                  this.$Message.success("删除成功");
                  this.refreshTable();
                  this.dblclickModal = false;
                } else {
                  this.$Message.error("删除失败");
                }
              });
          }
        });
      } else {
        this.$Message.error({
          content: "请选择至少一条"
        });
      }
    },
    addNewQues() {
      this.modalTitle = '新增题目'
      this.clickRecentQ = {};
      this.clickRecentQ.question = "";
      this.clickRecentQ.answer1 = "";
      this.clickRecentQ.answer2 = "";
      this.clickRecentQ.answer3 = "";
      this.clickRecentQ.answer4 = "";
      this.clickRecentQ.right = null;
      this.clickRecentQ.categoryId = null;
      this.clickRecentQ.categoryName = null;
      this.clickRecentQ.questionForm = 0;
      this.clickRecentQ.questionId = "new question";
      this.resetCrtBtn();
      this.clickRecentQ.isImg = false;
      this.clickRecentQ.url = "";

      this.isAddNew = true;
      this.inputDis = false;
      this.crtBtnType = "success";
      this.crtBtnTitle = "确定";

      this.dblclickModal = true;
    }
  }
};
</script>

