<style scoped>
.page-box {
}
.table-box {
    padding: 5px 20px;
}
.btn-box {
    text-align: left;
    margin: 5px auto;
}
.input-item {
    margin-bottom: 10px;
}
.input-item-title {
    font-weight: bold;
    margin-bottom: 3px;
}
</style>
<template>
  <div class="page-box">
    <sub-title title="学生管理" />

    <div class="table-box">
      <Row>
        <Col class="btn-box" span="24">
        <ButtonGroup>
          <Button type="primary" @click="openSearchModal">按条件查询</Button>
          <Button type="error" @click="deleteLotOfStus">批量删除</Button>
          <Button type="warning" @click="cleanSearch">清除查询</Button>
        </ButtonGroup>
        
        </Col>
      </Row>
      <Table @on-selection-change="tableSelectChange" :loading="tableLoading" @on-row-dblclick="clickRow" size="small"
        border :columns="columnsData" :data="tableData"></Table>
      <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
          <Page :total="tableTotal" :page-size="30" :current="currentPage" @on-change="changePage"></Page>
        </div>
      </div>
    </div>
    <Modal v-model="searchModal">
      <p slot="header">
        <span>按条件查询</span>
      </p>
      <Tabs v-model="searchInfo.recentTab">
        <TabPane label="按班级" name="tab1">
          <Cascader transfer style="width: 200px" :data="cascaderData" v-model="searchInfo.classDeptArr"></Cascader>
        </TabPane>
        <TabPane label="按年级" name="tab2">
          <Cascader transfer style="width: 200px" :data="gradeCascader" v-model="searchInfo.gradeDeptArr"></Cascader>
        </TabPane>
        <TabPane label="按院系" name="tab3">
          <Cascader transfer style="width: 200px" :data="academyCascader" v-model="searchInfo.academyDeptArr"></Cascader>
        </TabPane>
        <TabPane label="关键字" name="tab4">
          关键词：<Input v-model="searchInfo.keyWord" placeholder="搜索关键词" style="width: 300px" />
        </TabPane>
      </Tabs>
      <div slot="footer">
        <Button type="primary" @click="confirmSearch">搜索</Button>
      </div>
    </Modal>

    <Modal v-model="dblclickModal" width="500">
      <p slot="header">
        <span>学生详情</span>
      </p>
      <div>
        <Row>
          <Col class="input-item" span="24">
          <p class="input-item-title">姓名：</p>
          <Input :disabled="inputDis" v-model="clickRecentStu.name" placeholder="姓名" style="width: 300px" />
          </Col>
        </Row>
        <Row>
          <Col class="input-item" span="24">
          <p class="input-item-title">学号：</p>
          <Input disabled v-model="clickRecentStu.stuId" placeholder="学号" style="width: 300px" />
          </Col>
        </Row>
        <Row>
          <Col class="input-item" span="24">
          <p class="input-item-title">性别：</p>
          <RadioGroup v-model="clickRecentStu.sex">
            <Radio :label="0" :disabled="inputDis">男</Radio>
            <Radio :label="1" :disabled="inputDis">女</Radio>
          </RadioGroup>
          </Col>
        </Row>
        <Row>
          <Col class="input-item" span="24">
          <p class="input-item-title">院系/年级/班级：</p>
          <Cascader :disabled="inputDis" style="width: 300px" :data="cascaderData" v-model="clickRecentStu.deptArr"></Cascader>
          </Col>
        </Row>
      </div>
      <div slot="footer">
        <Button type="info" @click="correctPwd">修改学生密码</Button>
        <Button type="error" :loading="delete_loading" @click="deleteRecent">删除</Button>
        <Button :type="crtBtnType" :loading="correct_loading" @click="correctRecent">{{crtBtnTitle}}</Button>
      </div>
    </Modal>
  </div>

</template>

<script>
import SubTitle from "../components/SubTitle";
export default {
    data() {
        return {
            searchModal: false,
            newPwd: "",
            selectionArr: [],
            tableLoading: false,
            currentPage: 1,
            tableTotal: 0,
            delete_loading: false,
            correct_loading: false,
            crtBtnType: "warning",
            crtBtnTitle: "修改",
            dblclickModal: false,
            cascaderData: [],
            gradeCascader: [],
            academyCascader: [],
            clickRecentStu: {},
            inputDis: true,
            tableMode: "default",
            searchInfo: {
                confirmTab:'tab1',
                recentTab: "tab1",
                classDeptArr: [],
                gradeDeptArr: [],
                academyDeptArr: [],
                keyWord: ""
            },
            columnsData: [
                {
                    type: "selection",
                    width: 50,
                    align: "center"
                },
                {
                    title: "姓名",
                    key: "name"
                },
                {
                    title: "学号",
                    key: "stuId"
                },
                {
                    title: "性别",
                    width: 80,
                    key: "sexStr"
                },
                {
                    title: "院系",
                    key: "academy"
                },
                {
                    title: "年级",
                    key: "grade"
                },
                {
                    title: "班级",
                    key: "class"
                }
            ],
            tableData: []
        };
    },
    async beforeMount() {
        if (await this.getDeptInfo()) {
            this.getAllStusPerPage(1);
        }
        this.getDeptTree();
    },
    components: {
        SubTitle
    },
    methods: {
        cleanSearch() {
            this.getAllStusPerPage(1);
        },
        openSearchModal() {
            this.searchModal = true;
        },
        confirmSearch() {
            this.getSearchResult(1,this.searchInfo.recentTab);
        },
        getSearchResult(page,tab) {
            let deptType, deptId, keyWord;
            switch (tab) {
                case "tab1":
                    if (this.searchInfo.classDeptArr.length > 0) {
                        deptType = 5;
                        deptId = this.searchInfo.classDeptArr[2];
                    }
                    break;
                case "tab2":
                    if (this.searchInfo.gradeDeptArr.length > 0) {
                        deptType = 3;
                        deptId = this.searchInfo.gradeDeptArr[1];
                    }
                    break;
                case "tab3":
                    if (this.searchInfo.academyDeptArr.length > 0) {
                        deptType = 1;
                        deptId = this.searchInfo.academyDeptArr[0];
                    }
                    break;
                case "tab4":
                    keyWord = this.searchInfo.keyWord;
                    break;
                default:
                    break;
            }
            this.tableMode = "search";
            this.tableLoading = true;
            this.searchInfo.confirmTab = tab;
            this.$http
                .post("/admin/manage/queryUserList", {
                    deptType,
                    deptId,
                    userType: 5,
                    keyWord,
                    page,
                    row: 30
                })
                .then(res => {
                    if (res.data.status == 200) {
                        this.tableLoading = false;
                        let stusArr = res.data.data.userList;
                        this.tableTotal = res.data.data.count;
                        this.formatTableData(stusArr);
                        this.searchModal = false;
                    } else {
                        this.$Message.error("查询错误");
                    }
                });
        },
        formatCascaderData() {
            this.cascaderData = [];
        },
        getDeptTree() {
            this.$http.get("/admin/dept/getDeptNodeTree").then(res => {
                console.log("dept info", res.data);
                if (res.data.status == 200) {
                    let deptTree = res.data.data;
                    console.log("dept tree", deptTree);
                    let newArr1 = JSON.parse(JSON.stringify(deptTree));
                    let newArr2 = JSON.parse(JSON.stringify(deptTree));
                    let newArr3 = JSON.parse(JSON.stringify(deptTree));
                    newArr1 = moveUnToLast(newArr1)
                     newArr2 = moveUnToLast(newArr2)
                     newArr3 = moveUnToLast(newArr3)
                    this.cascaderData = traceDeptTree(newArr1, "normal");
                    this.gradeCascader = traceDeptTree(newArr2, "grade");
                    this.academyCascader = traceDeptTree(newArr3, "academy");
                } else {
                    this.$Message.error({
                        content: "网络错误"
                    });
                }
            });
        },
        getDeptInfo() {
            return new Promise((resolve, reject) => {
                this.deptMap = Object.create(null);
                this.$http.get("/admin/dept/getAllDept").then(res => {
                    console.log("dept info", res.data);
                    if (res.data.status == 200) {
                        let deptArr = res.data.data;
                        deptArr.forEach(item => {
                            this.deptMap[item.deptId] = item.deptName;
                        });
                        resolve(true);
                    } else {
                        this.$Message.error({
                            content: "网络错误"
                        });
                        reject(false);
                    }
                });
            });
        },
        filterUsers() {},
        getAllStusPerPage(page) {
            this.tableLoading = true;
            this.tableMode = "default";
            this.$http
                .get(`/admin/manage/getAllstudents?page=${page}&row=30`)
                .then(res => {
                    console.log("/admin/manage/getAllstudents", res.data);
                    if (res.data.status == 200) {
                        this.tableLoading = false;
                        let stusArr = res.data.data.userList;
                        this.tableTotal = res.data.data.count;
                        this.formatTableData(stusArr);
                    } else {
                        this.tableLoading = false;
                        this.$Message.error({
                            content: "网络错误"
                        });
                    }
                });
        },
        formatTableData(dataArr) {
            this.tableData = [];
            dataArr.forEach(item => {
                let temp = {};
                temp.name = item.realName;
                temp.sex = item.sex;
                temp.sexStr = item.sex > 0 ? "女" : "男";
                temp.userId = item.userId;
                temp.stuId = item.userName;
                temp.academy = this.deptMap[item.academyId];
                temp.grade = this.deptMap[item.gradeId];
                temp.class = this.deptMap[item.classId];
                temp.academyId = item.academyId;
                temp.gradeId = item.gradeId;
                temp.classId = item.classId;
                temp.reg = "统一导入";
                this.tableData.push(temp);
            });
        },
        changePage(index) {
            // 根据table 的模式，选择不同的页面切换方法
            // 分展示模式和搜索模式
            this.currentPage = index;
            console.log("页面切换了", index, this.currentPage);
            if (this.tableMode === "default") {
                this.getAllStusPerPage(this.currentPage);
            } else if (this.tableMode === "search") {
                this.getSearchResult(this.currentPage,this.searchInfo.confirmTab)
            }
        },
        clickRow(rowData) {
            this.resetCrtBtn();
            this.dblclickModal = true;
            this.clickRecentStu.name = rowData.name;
            this.clickRecentStu.userId = rowData.userId;
            this.clickRecentStu.stuId = rowData.stuId;
            this.clickRecentStu.sex = rowData.sex;
            this.clickRecentStu.deptArr = [
                rowData.academyId,
                rowData.gradeId,
                rowData.classId
            ];
            console.log(this.clickRecentStu.deptArr);
        },
        resetCrtBtn() {
            this.inputDis = true;
            this.crtBtnType = "warning";
            this.crtBtnTitle = "修改";
        },
        deleteRecent() {
            console.log("删除当前学生");
            this.$Modal.confirm({
                title: "删除确认",
                content: `请确认是否删除<b>${this.clickRecentStu.name}？</b>`,
                onOk: () => {
                    let userId = this.clickRecentStu.userId;
                    let arr = [];
                    arr.push(userId);
                    this.$http
                        .post("/admin/manage/deleteUsers", {
                            userIdJson: JSON.stringify(arr)
                        })
                        .then(res => {
                            if (res.data.status == 200) {
                                this.$Message.success("删除成功");
                                this.refreshRecentTable();
                                this.dblclickModal = false;
                            } else {
                                this.$Message.error("删除失败");
                            }
                        });
                }
            });
        },
        refreshRecentTable() {
            if (this.tableMode === "default") {
                this.getAllStusPerPage(this.currentPage);
            } else if (this.tableMode === "search") {
                this.getSearchResult(this.currentPage,this.searchInfo.confirmTab)
            }
        },
        correctRecent() {
            if (this.inputDis) {
                console.log("可以修改");
                this.inputDis = false;
                this.crtBtnType = "success";
                this.crtBtnTitle = "确定";
            } else {
                console.log("确认修改");
                
                let { stuId, name, userId, deptArr, sex } = this.clickRecentStu;
                if (this.deptMap[deptArr[0]]==='未分组') {
                    deptArr[1] = deptArr[0]
                    deptArr[2] = deptArr[0]
                }
                this.$http
                    .post("/admin/manage/updateTeacher", {
                        userId,
                        userType: 5,
                        userName: stuId,
                        realName: name,
                        sex,
                        academyId: deptArr[0],
                        gradeId: deptArr[1],
                        classId: deptArr[2]
                    })
                    .then(res => {
                        console.log("update user info ", res.data);
                        if (res.data.status == 200) {
                            this.$Message.success({
                                content: "修改成功"
                            });
                            this.refreshRecentTable();
                            this.dblclickModal = false;
                        } else {
                            this.$Message.error({
                                contetn: "修改失败"
                            });
                        }
                    });
            }
        },
        correctPwd() {
            console.log("修改当前学生密码");
            this.newPwd = "";
            this.$Modal.confirm({
                render: h => {
                    return h("Input", {
                        props: {
                            value: this.newPwd,
                            autofocus: true,
                            placeholder: "输入新密码"
                        },
                        on: {
                            input: val => {
                                this.newPwd = val;
                            }
                        }
                    });
                },
                onCancel: () => {
                    console.log("点了取消");
                    this.newPwd = "";
                },
                onOk: async () => {
                    console.log("点了确定");
                    if (this.newPwd.length > 5 && this.newPwd.length < 20) {
                        let {
                            stuId,
                            name,
                            userId,
                            deptArr
                        } = this.clickRecentStu;
                        this.$http
                            .post("/admin/manage/updateTeacher", {
                                userId,
                                userType: 5,
                                userName: stuId,
                                realName: name,
                                password: this.newPwd,
                                academyId: deptArr[0],
                                gradeId: deptArr[1],
                                classId: deptArr[2]
                            })
                            .then(res => {
                                console.log("update user info ", res.data);
                                if (res.data.status == 200) {
                                    this.$Message.success({
                                        content: "修改成功"
                                    });
                                    this.refreshRecentTable();
                                    this.dblclickModal = false;
                                } else {
                                    this.$Message.error({
                                        contetn: "修改失败"
                                    });
                                }
                            });
                    } else {
                        this.$Message.error("修改失败,请检查输入的密码");
                    }
                }
            });
        },
        deleteLotOfStus() {
            if (this.selectionArr.length > 0) {
                let number = this.selectionArr.length;
                this.$Modal.confirm({
                    title: "删除确认",
                    content: `请确认是否删除选中的${number}名学生？`,
                    onOk: () => {
                        let arr = [];
                        this.selectionArr.forEach(item => {
                            arr.push(item.userId);
                        });
                        this.$http
                            .post("/admin/manage/deleteUsers", {
                                userIdJson: JSON.stringify(arr)
                            })
                            .then(res => {
                                if (res.data.status == 200) {
                                    this.$Message.success("删除成功");
                                    this.refreshRecentTable();
                                    this.dblclickModal = false;
                                } else {
                                    this.$Message.error("删除失败");
                                }
                            });
                    }
                });
            } else {
                this.$Message.error({
                    content: "请选择至少一条"
                });
            }
        },
        tableSelectChange(selection) {
            console.log("selection", selection);
            this.selectionArr = selection;
        }
    }
};

function moveUnToLast(treeArr) {
  let newTreeArr = treeArr.slice(0);
  let unIndex = newTreeArr.findIndex(item => {
    return item.deptType === 0;
  });
  let unItem = newTreeArr[unIndex];
  newTreeArr.splice(unIndex, 1);
  newTreeArr.push(unItem);
  return newTreeArr;
}

function traceDeptTree(treeArr, traceType) {
    treeArr.forEach(item => {
        switch (traceType) {
            case "normal":
                break;
            case "grade":
                if (item.deptType == 3) {
                    delete item.children;
                }
                break;
            case "academy":
                if (item.deptType == 1) {
                    delete item.children;
                }
                break;

            default:
                break;
        }
        item.label = item.deptName;
        item.value = item.deptId;
        if (item.children && item.children.length > 0) {
            traceDeptTree(item.children, traceType);
        } else {
            delete item.children;
        }
    });

    return treeArr;
}
</script>